import axios from "axios"

export const GetWeather = ({ setWeatherTemp,setPop,dateRange,locationId,locationName}) => {
   //https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-D0047-069?Authorization=CWB-25DEA212-F3AA-4FDB-B05C-30DEF7E737DC&locationName=&timeFrom=2022-06-16T00:00:00&timeTo=2022-06-17T00:00:01
   // const data = axios.get(`https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-D0047-069?Authorization=CWB-25DEA212-F3AA-4FDB-B05C-30DEF7E737DC&locationName=&timeFrom=2022-06-16T00:00:00&timeTo=2022-06-17T00:00:01`)
   // const data = axios.get(`https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-D0047-069?Authorization=CWB-25DEA212-F3AA-4FDB-B05C-30DEF7E737DC&locationName=${dateRange}`)
   //add loction Id & location Name
   //   //dataRange =  &timeFrom=2022-06-17T00:00:00&timeTo=2022-06-18T00:00:01
   const data = axios.get(`https://opendata.cwb.gov.tw/api/v1/rest/datastore/${locationId}?Authorization=CWB-25DEA212-F3AA-4FDB-B05C-30DEF7E737DC&locationName=${locationName}${dateRange}`)
      .then((res) => {
         // console.log('All result',res.data)

         //allData Array ////[3] ()[6]
         let allData = res.data['records']['locations'][0].location[0]['weatherElement']
         
         //()
         let loopTime = res.data['records']['locations'][0].location[0]['weatherElement'][3].time.length;   
         
         let temp ={};
         let pop = {};
         for(let i = 0; i<loopTime; i++) {

            //obj{15: "30",18: "28",21: "27",Nextday00: "26"}
            //keyallData[3] 
            //dataTime2022-06-18 12:00:00,slice--> 12
            let objKeyName = allData[3].time[i].dataTime.slice(11, 13)

            //Number
            let tempData = Number(allData[3].time[i].elementValue[0].value)

            //:  30%27 1-2(2)94%
            let weatherDescription = allData[6].time[i].elementValue[0].value;
            //index
            let popIndex = weatherDescription.search("") 
            //slice(n,n+10)" 30%"
            //Number() --> persentgeOfPop = 30
            let percentageOfPop = Number(weatherDescription.slice(popIndex, popIndex + 10).replace(/[^0-9]/ig,""))
            //1212NextDay00keyNmae
            if(i===loopTime-1){ 
               temp["Nextday00"] = tempData;
               pop["Nextday00"] = percentageOfPop;
            }else{
               //objKeyName-->00,03,06,09,12,15......
               temp[objKeyName] = tempData;
               pop[objKeyName] =  percentageOfPop;
            }
         }
         //global statein App.js)
         setWeatherTemp(temp)
         setPop(pop)
         // console.log('TheTemp',temp,"POP",pop)
      })
}

